/* eslint-disable no-restricted-syntax */
/* eslint-disable no-unused-vars */
// capture.js
const menuCapture = new MenuCapture();

// Ferme le menuCapture et annule une capture s'il y en a une en cours.
function stopTout() {
  if (menuCapture.capture && menuCapture.capture.capture_en_cours) {
    menuCapture.capture.arreterCapture();
  }
  menuCapture.capture = null;
  menuCapture.cacher();
}

// Hack permettant d'obtenir l'lment sous le clic droit
let contextmenuCaptureElements = [];
document.addEventListener("contextmenuCapture", (event) => {
  contextmenuCaptureElements = document.elementsFromPoint(event.clientX, event.clientY) || [];
});

// Fermeture du menuCapture si clic dehors avant dbut de capture
document.addEventListener("click", (event) => {
  if (menuCapture.busy) return true;
  if (menuCapture.capture) return true;
  if (!menuCapture.visible) return true;
  if (document.querySelector("#poplab-popup").contains(event.target)) return true;
  menuCapture.cacher();
  return true;
});

document.addEventListener("keydown", (event) => {
  if (event.key === "Escape" && !menuCapture.busy) {
    event.preventDefault();
    // Ne pas fermer menuCapture lors de la slection de texte
    if (menuCapture.capture && menuCapture.capture.callback) {
      menuCapture.capture.arreterCapture();
      menuCapture.capture = null;
      return;
    }
    stopTout();
  }
  // eslint-disable-next-line consistent-return
  return true;
});

// On signale  Alsacrations que le webclipper est activ
// NOTE : A cause de scurits de l'API WebExtensions, on ne peut qu'envoyer un string.
// eslint-disable-next-line max-len
// Voir https://developer.mozilla.org/fr/docs/Web/JavaScript/Reference/Erreurs/Property_access_denied
if (window.location.host === "poplab.education") {
  window.dispatchEvent(
    new CustomEvent("poplab:webclipper_active", { detail: VERSION }),
  );
}

browser.runtime.onMessage.addListener(async (msg, sender) => {
  // Voir background.js:notifier_onglet_actif()
  if (msg.images) {
    menuCapture.images = msg.images;
  }

  if (msg.first_name) {
    menuCapture.first_name = msg.first_name;
  }

  if (msg.type === "cancelUpload") {
    menuCapture.busy = false;
    menuCapture.div.classList.remove("disabled");
    menuCapture.old_capture.envoyerCapture(menuCapture.old_capture.data);
    return;
  }

  if (msg.type === "startCapture") {
    if (!menuCapture.busy && !menuCapture.capture && !menuCapture.visible) {
      menuCapture.afficher();
    }
    return;
  }

  if (msg.type === "stopCapture") {
    stopTout();
    return;
  }

  if (msg.type === "remercier") {
    if (!menuCapture.capture) return;
    menuCapture.remercier();
    return;
  }

  if (msg.type === "toggleCapture") {
    if (menuCapture.busy) return;

    if (menuCapture.capture) {
      menuCapture.capture.arreterCapture();
      menuCapture.capture = null;
      menuCapture.cacher();
      return;
    }

    if (menuCapture.visible) {
      menuCapture.cacher();
    } else {
      menuCapture.afficher();
    }
    return;
  }

  if (msg.type === "capturerImage") {
    if (menuCapture.busy) return;
    for (const element of contextmenuCaptureElements) {
      if (element.tagName === "IMG") {
        // capture.js
        new CaptureImage(menuCapture).capturer(element);
        return;
      }
    }
  }

  if (msg.type === "capturerLien") {
    if (menuCapture.busy) return;
    for (const element of contextmenuCaptureElements) {
      if (element.tagName === "A") {
        // capture.js
        new CaptureLien(menuCapture).capturer(element);
        return;
      }
    }
  }

  if (msg.type === "capturerVideo") {
    if (menuCapture.busy) return;
    for (const element of contextmenuCaptureElements) {
      if (element.tagName === "VIDEO") {
        // capture.js
        new CaptureVideo(menuCapture).capturer(element);
        return;
      }
    }
  }
});
